// HAMBURGER MENU

export default function Navbar({ currentPage, handlePageChange }) {

  const [isNavOpen, setIsNavOpen] = useState(false);

  // NAVBAR ITEMS
  const navbar = [
    {
      navName: "About Me",
      navLink: "#aboutMe"
    },
    {
      navName: "Portfolio",
      navLink: "#portfolio"
    },
    {
      navName: "Contact",
      navLink: "#contact"
    },
    {
      navName: "Resume",
      navLink: "#resume"
    }
  ].map(navItems => ({ ...navItems, isActive: navItems.navName === currentPage }));


  return (
    <div className="flex items-center justify-between bg-white border-gray-200 py-8 px-2.5 rounded text-sm">
      {/* <a href="/">
        <img src="https://designbygio.it/images/logo.png" alt="logo" />
      </a> */}
      <span className="self-center text-xl font-semibold whitespace-nowrap dark:text-white">Brian Alegre</span>
      <nav>
        <section className="MOBILE-MENU flex lg:hidden">
          <div
            className="HAMBURGER-ICON space-y-2"
            onClick={() => setIsNavOpen((prev) => !prev)}
          >
            <span className="block h-0.5 w-8 animate-pulse bg-black"></span>
            <span className="block h-0.5 w-8 animate-pulse bg-black"></span>
            <span className="block h-0.5 w-8 animate-pulse bg-black"></span>
          </div>

          <div className={isNavOpen ? "showMenuNav" : "hideMenuNav"}>

            <div
              className="CROSS-ICON absolute top-0 right-0 px-8 py-8"
              onClick={() => setIsNavOpen(false)}
            >
              <svg
                className="h-8 w-8 text-gray-600"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <line x1="18" y1="6" x2="6" y2="18" />
                <line x1="6" y1="6" x2="18" y2="18" />
              </svg>
            </div>
            <ul className="MENU-LINK-MOBILE-OPEN flex flex-col items-center justify-between min-h-[250px]">
              {/* <li className="border-b border-gray-400 my-8 uppercase">
                <a href="/about">About</a>
              </li>
              <li className="border-b border-gray-400 my-8 uppercase">
                <a href="/portfolio">Portfolio</a>
              </li>
              <li className="border-b border-gray-400 my-8 uppercase">
                <a href="/contact">Contact</a>
              </li> */}
              {navbar.map(navItem => (
                <li
                  key={navItem.navName}
                  className={`${navItem.isActive ? "text-orange-500" : "text-gray-700 md:hover:text-orange-500"}` + " flex items-center mr-4"}

                  // ONCLICK HANDLE PAGE CHANGE
                  onClick={() => handlePageChange(navItem.navName)}
                >
                  {navItem.navName}
                </li>
              ))}
            </ul>
          </div>
        </section>

        <ul className="DESKTOP-MENU hidden space-x-8 lg:flex">
          {navbar.map(navItem => (
            <li
              key={navItem.navName}
              className={`${navItem.isActive ? "text-orange-500" : "text-gray-700 md:hover:text-orange-500"}` + " flex items-center mr-4"}

              // ONCLICK HANDLE PAGE CHANGE
              onClick={() => handlePageChange(navItem.navName)}
            >
              {navItem.navName}
            </li>
          ))}
        </ul>
      </nav>
      <style>{`
      .hideMenuNav {
        display: none;
      }
      .showMenuNav {
        display: block;
        position: absolute;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        background: white;
        z-index: 10;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
      }
    `}</style>
    </div>
  );
}
